// 引入axios以及element ui中的loading和message组件
import axios from 'axios'
import sha1 from 'js-sha1'
//import router from './index'


//那么 showFullScreenLoading() tryHideFullScreenLoading() 要干的事儿就是将同一时刻的请求合并。
//声明一个变量 needLoadingRequestCount，每次调用showFullScreenLoading方法 needLoadingRequestCount + 1。
//调用tryHideFullScreenLoading()方法，needLoadingRequestCount - 1。needLoadingRequestCount为 0 时，结束 loading。


//检测状态码
function checkCode(num){
    var s = num + '';
    if(s.length == 4){
        if(s.charAt(0) == '4'){
            return true;
        }
    }else{
        return false;
    }
}


// http request 拦截器
axios.interceptors.request.use(
    config => {
        var now = Date.now();
        var appKey = sha1("A6059291924821"+"UZ"+"C3E1F8FB-FB1F-CEE3-2EBB-D66BB9E7614F"+"UZ"+now)+"."+now;
        
        //console.log(appKey)
        //config.headers['X-APICloud-AppId'] = 'A6059291924821';
        //config.headers['X-APICloud-AppKey'] = appKey;
        config.headers['Content-type'] = 'application/x-www-form-urlencoded';
        config.headers['Accept'] = 'application/json';
        return config;
    },
    err => {
        return Promise.reject(err);
});

// http response 拦截器
axios.interceptors.response.use(
    response => {
        /*if(checkCode(response.data.code)){
            // 401 清除token信息并跳转到登录页面
            store.commit(types.LOGOUT);
            router.replace({
                path: '/login',
                replace: {redirect: router.currentRoute.fullPath}
            })
        }*/
        
        return response;
    },
    error => {
        if (error.response) {
            /*switch (error.response.status == 'error') {
                case 404:
                    // 401 清除token信息并跳转到登录页面
                    store.commit(types.LOGOUT);
                    router.replace({
                        path: 'login',
                        query: {redirect: router.currentRoute.fullPath}
                    })
            }*/
        }
        // console.log(JSON.stringify(error));//console : Error: Request failed with status code 402
        return Promise.reject(error.response)
    });

export default axios;



