import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

//首页
const Home = (resolve) => {
  import('@/components/home/home.vue').then((module) => {
    resolve(module)
  })
}

//登录注册页面
const Login = (resolve) => {
  import('@/components/login/login.vue').then((module) => {
    resolve(module)
  })
}

//天气日期模块
const Weather = (resolve) => {
  import('@/components/weather/weather.vue').then((module) => {
    resolve(module)
  })
}

//个人信息模块
const User = (resolve) => {
  import('@/components/user/user.vue').then((module) => {
    resolve(module)
  })
}

//视频模块
const Video = (resolve) => {
  import('@/components/video/video.vue').then((module) => {
    resolve(module)
  })
}

//时间模块
const Time = (resolve) => {
  import('@/components/time/time.vue').then((module) => {
    resolve(module)
  })
}

//导航模块
const Maps = (resolve) => {
  import('@/components/map/map.vue').then((module) => {
    resolve(module)
  })
}


export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/weather',
      name: 'Weather',
      component: Weather
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/user',
      name: 'User',
      component: User
    },
    {
      path: '/video',
      name: 'Video',
      component: Video
    },
    {
      path: '/time',
      name: 'Time',
      component: Time
    },
    {
      path: '/map',
      name: 'Maps',
      component: Maps
    }
  ]
})



